<template>
    <div class="login-right">
        <div class="login-content">
            <el-form ref="form" :model="form" :rules="rules" label-width="80px" label-position="left">
                <el-form-item label="用户名：" prop="userName">
                    <el-input v-model="form.userName"></el-input>
                </el-form-item>
                <el-form-item label="密码：" prop="password">
                    <el-input type="password" v-model="form.password"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary"
                               v-loading="loading"
                               element-loading-text="登陆中"
                               element-loading-spinner="el-icon-loading"
                               @click="onSubmit">登录
                    </el-button>
                    <el-button type="primary" @click="$router.push('register')">注册</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
import {login} from "@/api/userinfo"
import {setToken, getToken, getUserInfo, setUserInfo} from "@/utils/auth";

export default {
    name: 'login-right',
    data() {
        return {
            loading: false,
            form: {
                userName: '',
                password: ''
            },
            rules: {
                userName: [
                    {required: true, message: '请输入用户名称', trigger: 'blur'},
                    {min: 3, max: 15, message: '长度在 3 到 15 个字符', trigger: 'blur'}
                ],
                password: [
                    {required: true, message: '请输入您的密码', trigger: 'blur'},
                    {min: 7, max: 16, message: '长度在 7 到 16 个字符', trigger: 'blur'}
                ],

            }
        }
    },
    methods: {
        onSubmit() {
            let _self = this;
            login(this.form).then(res => {
                let state = res.data.code;
                //console.log(res.data.token, res.status)
                //console.log(state)
                if (state === 200) {
                    this.loading = true;
                    setToken(res.data.token);
                    setUserInfo(res.data.data.userid);
                    this.$store.commit('SET_TOKEN', getToken());
                    this.$store.commit('SET_USERINFO', getUserInfo());
                    this.$router.push('navContent');
                } else {
                    if(res.data.code === 1001) {
                        _self.$message({
                            type: 'warning',
                            message: res.data.message + ',请先注册！'
                        });
                        return
                    }
                    _self.$message({
                        type: 'warning',
                        message: res.data.message
                    });

                }
                // eslint-disable-next-line no-unused-vars
            }).catch(err => {
            });
        }
    }

};
</script>
<style scoped lang="less">
.login-right {
    width: 460px;
    height: 300px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    .login-content {
        width: 460px;
        padding: 20px 40px 20px 40px;
        position: relative;

        .login-title {
            width: 100%;
            height: 32px;
            line-height: 30px;
            font-size: 20px;
            color: #00abab;
            display: flex;
            justify-content: center;

        }
    }
}

</style>